// 공통 - 트리스크립트
<template>
  <div>
    <v-treeview
      v-model="tree"
      :open.sync="open"
      :items="list"
      :active.sync="activeItemList"
      :multiple-active="isMultiple"
      :activatable="activatable"
      item-key="key"
      item-text="text"
      item-children="child"
      active-class="active activeColor"
      expand-icon="expand_more"
    >
      <template slot="prepend" slot-scope="{  item, /*open,  leaf*/  }">
        <div class="treeTypeBox green white--text">{{ item.type }}</div>
      </template>
    </v-treeview>
  </div>
</template>

<script>
export default {
  props: ["list", "parentThis", "onCreate", "onDelete", "onUpdate", "onClick", "activeList","isMultiple", "isActivatable"],
  data: () => ({
    open: [],
    activeItemList: [],
    tree: [],
    activatable : true
  }),
  watch: {
    activeItemList: {
      immediate: false,
      handler: function(newData, oldData) {
          var itemList = [];
          // 부모 this로 바인딩
          if(this.parentThis){
            for(var i in newData){
              itemList.push(this.searchItem(newData[i]))
            }
            this.onClick.call(this.parentThis, { itemList: itemList });
          }
      }
    },
    open: function(newData, oldData) {
    },
    activeList : function(){
      this.activeItemList = this.activeList;
    }
  },
  beforeMount(){
    if(this.isActivatable === false){
      this.activatable = false;
    }
  },
  methods : {
    searchItem : (function(){
      var _key, result;
      var search = function(list){
        if(result != null){
          return;
        }
        for(var i in list){
          if(list[i].key == _key){
            result = list[i];
            return list[i];
          }
          if(list[i].child){
            search(list[i].child)
          }
        }
      }

      return function(key){
        result = null;
        _key = key;
        search(this.list);
        return result;
      }
    })()
  }
};
</script>
<style slot-scope>
.treeTypeBox{
  width: 30px;
  height:30px;
  line-height: 28px;
  text-align: center;
  vertical-align: middle;
  margin:0 5px;
}
.v-treeview-node{
  margin-top:5px;
    padding-top:5px;
  padding-bottom:5px;
}
.v-treeview-node__root{
  height:auto;
}
.v-treeview-node__content *{
  cursor: pointer;
}
</style>